"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { toast } from "sonner"

export default function AdminReportsPage() {
  const [reports, setReports] = useState<any[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchReports()
  }, [])

  const fetchReports = async () => {
    try {
      const res = await fetch("/api/admin/reports")
      const data = await res.json()
      setReports(data.reports || [])
    } catch (error) {
      toast.error("Failed to load reports")
    } finally {
      setLoading(false)
    }
  }

  const handleReport = async (id: string, action: string) => {
    try {
      const res = await fetch(`/api/admin/reports/${id}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ action }),
      })
      if (res.ok) {
        toast.success(`Report ${action}`)
        fetchReports()
      }
    } catch (error) {
      toast.error("Failed to handle report")
    }
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold">User Reports</h1>

      <Card>
        <CardHeader>
          <CardTitle>Pending Reports</CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <p>Loading...</p>
          ) : reports.length === 0 ? (
            <p>No pending reports</p>
          ) : (
            <div className="space-y-4">
              {reports.map((report) => (
                <div key={report.id} className="border p-4 rounded space-y-2">
                  <div className="flex justify-between">
                    <div>
                      <h3 className="font-bold">{report.type}</h3>
                      <p className="text-sm">{report.reason}</p>
                      <p className="text-xs text-muted-foreground">
                        Reported by: {report.reporter_id} | Target: {report.target_id}
                      </p>
                    </div>
                    <span className="text-xs">{new Date(report.created_at).toLocaleDateString()}</span>
                  </div>
                  <div className="flex gap-2">
                    <Button size="sm" onClick={() => handleReport(report.id, "resolved")}>
                      Resolve
                    </Button>
                    <Button size="sm" variant="destructive" onClick={() => handleReport(report.id, "dismissed")}>
                      Dismiss
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
